<template>
  <div class="content">

    <countdown :time="countdown" @countdownend="countdownend" class="task-countdown">
      <template slot-scope="props"><van-icon name="hint"/>任务将在{{ props.hours || '00' }}小时{{ +props.minutes || '00' }}分{{ +props.seconds || '00' }}秒后到期</template>
    </countdown>

    <div class="pay_way_group" style="margin-top: 30px">
      <div class="pay_way_title">销量任务</div>
      <van-cell-group class="payment_group">
        <van-cell title="店铺名称">{{salesTask.goods.shop_name}}</van-cell>
        <van-cell style="color: red;">
          <template slot="title">
            <span class="van-cell-text">关键字</span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(1)"/>
          </template>
          <template onselectstart="return false;" class="no-longtap" ontouchstart="return false;">
            <span class="van-cell-text no-longtap"  onselectstart="return false;" ontouchstart="return false;">{{salesTask.goods.key}}</span>
          </template>
        </van-cell>
        <van-cell title="宝贝单价">
          {{salesTask.goods.goods_price}}
        </van-cell>
        <van-cell title="">
          <template slot="title">
            <span class="van-cell-text">宝贝数量</span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(2)"/>
          </template>
          {{salesTask.goods.num}}
        </van-cell>
        <van-cell title="">
          <template slot="title">
            <span class="van-cell-text">应付金额</span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(5)"/>
          </template>
          {{salesTask.goods.goods_price * salesTask.goods.num | money}}
        </van-cell>
        <!--<van-cell title="客服改价">{{salesTask.revisePrice}}</van-cell>-->
        <!--<div class="van-cell"><span>商品主图</span></div>-->
        <van-cell title="">
          <template slot="title">
            <span class="van-cell-text">商品主图</span>
            <van-icon slot="right-icon" name="question" class="van-cell__right-icon" @click="showHelp(3)"/>
          </template>
        </van-cell>
      </van-cell-group>
      <img v-lazy="salesTask.goods.main_img" width="100%" @click="showImagePreview(salesTask.goods.main_img)">
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">任务要求</div>
      <van-cell-group class="payment_group">
        <!--<van-cell title="搜索关键字">{{salesTask.searchKey}}</van-cell>-->
        <!--<van-cell title="排名方式">{{salesTask.sortord}}</van-cell>-->
        <van-cell title="地区">{{salesTask.task.address}}</van-cell>
        <van-cell title="价格区间">{{salesTask.task.range}}</van-cell>
        <van-cell title="浏览时间">{{salesTask.task.browsing_time}}</van-cell>
        <van-cell title="是否收藏">{{salesTask.task.conllect}}</van-cell>
        <van-cell title="加购物车">{{salesTask.task.shopping_cart}}</van-cell>
        <van-cell title="是否假聊">{{salesTask.task.chat == '是' ? salesTask.task.chat_remark : '否'}}</van-cell>
        <van-cell title="备注">{{salesTask.task.remark || '无'}}</van-cell>
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">店铺验证</div>
      <van-cell-group class="payment_group">
        <van-field v-model="shopName" type="text" placeholder="请输入完整的店铺名进行验证" :error="errorInfo.shopName">
          <van-button slot="button" size="small" type="primary" @click="validShopName()">验证店铺名称</van-button>
        </van-field>
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">上传1个其他宝贝链接<span style="color: darkred; margin-left: 5px;">如何获取宝贝链接</span><van-icon @click="showHelp(4)" slot="right-icon" name="question" class="van-cell__right-icon" /></div>
      <p class="text_style">
        请花2-3分钟浏览2-3款其他店铺的宝贝，并上传1个宝贝的链接
      </p>
      <p class="text_red">
        商家后台可检测，请浏览足够时间，时间不足将可能扣除此单佣金奖励
      </p>
      <van-cell-group>
        <van-field v-model="others1" type="textarea"  placeholder="请输入宝贝链接或淘宝口令" :error="errorInfo.others1"></van-field>
        <!-- <van-field v-model="others2"  placeholder="请输入宝贝链接2" :error="errorInfo.others2"></van-field>
        <van-field v-model="others3"  placeholder="请输入宝贝链接3"  :error="errorInfo.others3"></van-field> -->
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">上传店内另一宝贝链接</div>
      <p class="text_style">
        请花2-3分钟浏览主宝贝，并花1-2分钟浏览店铺里其他宝贝(副款)，<br>并上传1个副款宝贝的连接
      </p>
      <p class="text_red">
        商家后台可检测，请浏览足够时间，时间不足将可能扣除此单佣金奖励
      </p>
      <van-cell-group class="payment_group">
        <van-field v-model="theShop" type="textarea" placeholder="请输入宝贝链接或淘宝口令" :error="errorInfo.theShop"></van-field>
      </van-cell-group>
    </div>

    <div v-if="salesTask.pick_details.length > 0" class="pay_way_group" style="padding-bottom: 10px;">
      <div class="pay_way_title">详情找茬</div>
      <van-row class="upload_hint text-desc">
        <van-icon name="hint"/>
        详情找茬: 请在商品详情页中搜索以下文案出现的位置，复制开头/结尾中间的文本内容
      </van-row>
      <van-row class="vacancy_row" v-for="(pick, i) in salesTask.pick_details" :key="i">
        <van-col span="8">{{pick.front}}</van-col>
        <van-col span="8">
          <input :ref="'inputDetail' + i" type="text" placeholder="请补充详情内容" class="van-field__control"  :pid="pick.id">
        </van-col>
        <van-col span="8">{{pick.ending}}</van-col>
      </van-row>
    </div>

    <div v-if="salesTask.pick_comments.length > 0" class="pay_way_group" style=" padding-bottom: 10px;">
      <div class="pay_way_title">评论找茬</div>
      <van-row class="upload_hint text-desc">
        <van-icon name="hint"/>
        评论找茬: 请在商品评论中查找以下评论的日期，格式：201806012530
      </van-row>
      <van-row class="vacancy_row" v-for="(pick, i) in salesTask.pick_comments" :key="i">
        <van-col span="8">{{pick.comments}}</van-col>
        <van-col span="16"><input :ref="'inputComment' + i" type="text" class="van-field__control"  placeholder="请输入评论日期" :pid="pick.id"></van-col>
      </van-row>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">订单验证:<span style="color: darkred;">付款金额要在任务金额的±100内</span></div>
      <p v-if="orderType == 1" class="text_style">
        请提交已拍下未付款的订单号进行审核；提交审核之后，管理员会在1小时内将宝贝款项打到您的银行卡中，收到款之后请立即付款
      </p>

      <p v-if="orderType == 2" class="text_style">
        请提交已拍下未付款的订单号进行验证。<br>温馨提示：订单号能提交成功，就意味着您拍下的宝贝是正确的。
      </p>
      <van-cell-group class="payment_group">
          <van-field v-model="orderNo" type="text"  placeholder="请输入您拍下的订单号" :error="errorInfo.orderNo" />
      </van-cell-group>
    </div>

    <div class="pay_way_group">
      <div class="pay_way_title">淘宝实际金额:<span style="color: darkred;"></span></div>
      <van-cell-group class="payment_group">
          <van-field v-model="tbMoney" type="text"  placeholder="请输入淘宝实际付款金额"/>
      </van-cell-group>
    </div>

    <countdown v-if="counting" slot="button" :time="180000" @countdownend="countdownend" class="countdown van-button">
      <template slot-scope="props">{{ +props.minutes || '00' }}分{{ +props.seconds || '00' }}秒后继续</template>
    </countdown>

    <van-button v-else class="pay_submit" @click="submit" :loading="isSubmit"
                type="danger" :bottom-action="true">提交审核
    </van-button>

    <van-button class="cannel_submit" @click="cancel" type="default" :bottom-action="true">关闭订单
    </van-button>
  </div>
</template>

<script>
  import {
    Row,
    Col,
    CellGroup,
    Cell,
    Button,
    Dialog,
    ImagePreview,
    Toast,
    NavBar,
    Field
  } from "vant";
  import { SALES, CANCEL, VALID_SHOP} from "@/api/task";
  import img01 from '@/assets/images/img01.png'
  import img02 from '@/assets/images/img02.png'
  import img03 from '@/assets/images/img03.png'
  import img04 from '@/assets/images/img04.png'
  import img05 from '@/assets/images/img05.png'
  import img06 from '@/assets/images/img06.png'
  import img07 from '@/assets/images/img07.png'

  export default {
    name: "salesDetails",
    data() {
      return {
        isSubmit : false,
        countdown: 210000,
        shopName :'',
        others1 : '',
        theShop:'',
        tbMoney:'',
        orderNo: '',
        orderType: '',
        counting: true,
        salesTask: {
          "task": {
            "id": "",
            "type": "",
            "overtime": "",
            "conllect": "",
            "shopping_cart": "",
            "chat": "",
            "screenshot_pay": "",
            "screenshot_search": "",
            "address": "",
            "range": "",
            "browsing_time": "",
            "chat_remark":"",
            "remark": "",
            "status": "",
            "othersExample": "",
          },
          "goods": {
            "id": "",
            "key": "",
            "main_img": "",
            "price": "",
            "shop_name": ""
          },
          "pick_details": [
            {
              "id": "",
              "front": "",
              "ending": "",
              "answer": ""
            }
          ],
          "pick_comments": [
            {
              "id": "",
              "comments": "",
              "answer": ""
            }
          ]
        },
        errorInfo: {
          shopName: false,
          others1:false,
          theShop: false,
          orderNo: false
        }
      };
    },
    props: {
      id: this.id
    },
    watch:{
			'$route': 'changeRouter'
		},
    created(){
      this.initPage();
    },

    methods: {
      copytouch(e,s) {
        e.preventDefault=true
        alert('不可复制')
      },
      initPage(){
        if(!this.id){
          return;
        }
        this.$reqGet(SALES + this.id).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        let result = res.data;
        if(!result || result.code != 0){
          let msg = result.msg ? result.msg : "获取任务失败";
          Toast.fail(msg);
          return;
        }
        let data = result.data;
        this.salesTask = data;
        this.orderType = data.task.order_type;
        this.countdown = this.salesTask.task.overtime;
      },
      validShopName(){
        if(!this.shopName){
          Toast.fail('请输入店铺名称');
          return;
        }
        this.$reqPost(VALID_SHOP, {
          order_id:this.id,
          shop_name:this.shopName
        }).then((res) => {
          Toast(res.data.msg);
        }).catch((err) => {
          Toast(err.data.msg);
        });
      },
      submit() {
        let details = this.salesTask.pick_details;
        let check = true;
        let items = [
          'shopName',
          'others1',
          'theShop',
          'orderNo'
        ];
        check = items.every(item => {
          let msg = this.getErrorMessage(item);
          if (msg) {
            this.errorInfo[item] = true;
            Toast.fail(msg);
          }
          return !msg;
        });
        if(!check){
          Toast.fail('参数错误');
        }
        let formdata = new FormData();
        formdata.append("order_id", this.id);
        formdata.append("tb_order_id", this.orderNo);
        formdata.append("others1", this.others1);
        formdata.append("shopName", this.shopName);
        formdata.append("theShop", this.theShop);
        formdata.append("tbMoney", this.tbMoney);
        this.$reqPost(SALES, formdata).then((res) => {
          this.submitSuccess(res);
        }).catch((err) => {

        });
      },
      getErrorMessage(key) {
        switch (key) {
          case "shopName":
            return this.shopName ? "" : "请填写完整店铺名称";
          case "others1":
            return this.others1 ? "" : "请上传3个其他店铺宝贝链接";
          case "theShop":
            return this.theShop ? "" : "请上传店内另一宝贝链接";
          case "orderNo":
            return this.orderNo ? "" : "请输入您拍下的订单号";
        }
      },
      submitSuccess(res) {
        let data = res.data;
        if(this.orderType == 1){
            var tipStr=`<p style="text-align: center;">订单号提交成功，商家会在1小时内将应付的金额转入您的银行卡中；请你收到款后尽快付款。</p>
            <p style="color: darkred; text-align: center;">（请勿使用花呗或信用卡支付，否则将赔付商家1%手续费）</p>`
        }
        if(this.orderType == 2){
            var tipStr=`<p style="text-align: center;">订单号提交成功，请您前往手淘付款。</p>
            <p style="color: darkred; text-align: center;">（如果您拍了多个订单号，请不要付款错误，任务提交的是哪个订单号，就付款哪个订单哦~）</p>
            <p style="color:red;text-align:center">(请勿使用花呗或信用卡支付，否则将赔付商家1%手续费)</p>
            `
        }
        Dialog.alert({
          message:tipStr
        }).then(() => {
           this.$router.push({ path: "/orders"})
        }).catch((err) => {
          // console.log(err);
        });
      },

      showImagePreview(img){
        ImagePreview([img]);
      },
      countdownend() {
        this.counting = false;
      },

      cancel(){
        Dialog.confirm({
          title: '提示',
          message: '您确定要关闭订单吗？'
        }).then(() => {
          // on confirm
          this.$reqPost(CANCEL, {
            order_id:this.id
          }).then((res) => {
            Toast(res.data.msg);
            if(res.data.code == 0){
              this.$router.go(-1);
            }
          }).catch((err) => {
            Toast(err.data.msg);
            if(err.data.code == 0){
              this.$router.go(-1);
            }
          });
        }).catch(() => {
          // on cancel
        });
      },

      showHelp(type){
        switch (type){
          case 1:
            Toast({
              message: '必须用指定的关键词来找到宝贝。如果找不到宝贝请联系微信客服求助',
              position: 'top'
            });
            break;
          case 2:
            Toast({
              message: '必须拍下指定件数，否则无法提交任务',
              position: 'top'
            });
            break;
          case 3:
            Toast({
              message: '请根据关键字找到和下图一样的宝贝',
              position: 'top'
            });
            break;
          case 4:
            // this.showImagePreview(this.salesTask.task.othersExample);
            ImagePreview([
              img01,
              img02,
              img03,
              img04,
              img05,
              img06,
              img07
            ]);
            break;
          case 5:
            Toast({
              message: '应付金额=宝贝单价*宝贝数量（应付金额允许存在正负100的差价）',
              position: 'top'
            });
            break;
        }
      }
    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Button.name]: Button,
      [ImagePreview.name]: ImagePreview,
      [Toast.name]: Toast,
      [NavBar.name]:NavBar,
      [Field.name]:Field
    },

    filters: {
      money: function(val) {
        val = val.toString().replace(/\$|\,/g,'');
        if(isNaN(val)) {
          val = "0";
        }
        let sign = (val == (val = Math.abs(val)));
        val = Math.floor(val*100+0.50000000001);
        let cents = val%100;
        val = Math.floor(val/100).toString();
        if(cents<10) {
          cents = "0" + cents
        }
        for (var i = 0; i < Math.floor((val.length-(1+i))/3); i++) {
          val = val.substring(0,val.length-(4*i+3))+',' + val.substring(val.length-(4*i+3));
        }
        return (((sign)?'':'-') + val + '.' + cents);
      }
    }
  };

</script>

<style lang="scss" scoped>

  // .payment_group {
  //   /*padding: 10px 15px;*/
  // }

  .task-countdown {
    text-align: center;
    font-size: 12px;
    background-color: #fffeec;
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    z-index: 999;
  }

  .pay_submit {
    /*position: fixed;*/
    /*bottom: 0;*/
    width: 100%;
  }

  .cannel_submit{
    margin-top:10px;
    width: 100%;
  }

  .pay_way_group{
    background-color: #ffffff;
    margin-bottom: 10px;
  }

  .pay_way_group img {
    vertical-align: middle;
  }

  .pay_way_title {
    font-weight: bold;
    padding: 15px;
    background-color: #fff;
  }

  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    margin: 10px;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .vacancy_row{
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 0px;
      background-clip: content-box;
    }
  }

  .countdown{
    text-align: center;
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #29a0f6;
  }

  .van-cell__right-icon {
    top: 3px;
  }

  .text_style {
    color: #29a0f6;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .text_red{
    color:red;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .no-longtap{
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
</style>



